import React,{Fragment} from 'react'
import {ActivityIndicator, Icon, NavBar} from "antd-mobile";
import {connect} from 'react-redux'
import {getOhtersInfo} from "../../../api/user";
class OthersInfo extends React.Component{

    constructor(props){
        super(props)
        this.state={
            type:true,
            info:null
        }
    }
    componentDidMount() {
        this.getInfo(this.props.match.params.id)
    }
    getInfo(id){
        getOhtersInfo(id).then(res=>{
            this.setState({
                type:false,
                info:res.data
            })
        })
    }
    render() {
        let {info} = this.state
        const row = ()=>{
            if (info){
                return (
                    <Fragment>
                        <li style={{position:'relative'}}> {this.state.type?<input type="file" onChange={()=>this.ImagePicker(info[0])} ref={img=>this.lv=img} accept='image/*'/>:''}<img ref={img=>this.img=img} src={info.user_imag?info.user_imag:info.user_image?info.user_image:'https://www.fengjinqi.com/static/img/pc-icon.png'} alt=""/></li>
                        <li>
                            <span>{info.username}</span>
                            </li>
                        <li><span>{info.position}</span></li>
                        <li>  <span>{info.info}</span></li>
                    </Fragment>

                )
            }
        }
        return(
            <Fragment>
                <ActivityIndicator toast text=""animating={this.state.type}  />
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                ></NavBar>
                <ul className='container info'>
                    {row()}
                </ul>
            </Fragment>
        )
    }

}
const mapState = (state)=>({
})
const mapDispatch =(dispatch)=>({


})
export default connect(mapState,mapDispatch)(OthersInfo)