/**
 * 
 * @param id
 */
export const otherArticles = (id)=>{
    return axios.request({
        url:`/api/PersonOthers/?pk=${id}`,
        method:'get'
    })
}
/**
 * 
 * @param id
 * @param page
 */
export const otherArticlesPage = (id,page)=>{
    return axios.request({
        url:`/api/PersonOthers/?pk=${id}&page=${page}`,
        method:'get'
    })
}