import React,{Component,Fragment}  from 'react'
import PropTypes from 'prop-types';
import {Icon, NavBar,List,Modal,WingBlank} from "antd-mobile";
import {Link} from "react-router-dom";
import {getToken} from '../utils/utils'
import Button from "antd-mobile/lib/button";
const Item = List.Item
const alert = Modal.alert;
 class LoginMain extends Component{

    render() {
        console.log(this.props)
        return(

            <Fragment>
                {this.props.type?  <div>
                    <NavBar
                        mode="dark"
                        icon={<Icon type="left" />}
                        onLeftClick={() => this.props.history.goBack()}
                        rightContent={
                            this.props.name?<Link to='/person/message' style={{color:'#fff'}}><div className=' fa  fa-commenting-o'>{this.props.message.count>0?<b className='commenting'></b>:''}</div></Link>:''
                        }
                    ></NavBar>
                    <div className="container">
                        <div className="container-header">
                            <div className='container-header-main'>
                                <img src={this.props.image} alt=""/>
                                {this.props.name?<Link to='/person/info' className='user'><span>{this.props.name}</span><Icon type='right
'/></Link>:<Link to='/login' className='user'>/</Link>}

                            </div>
                        </div>

                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {getToken()?this.props.history.push('/person/article'):this.props.history.push('/login')}}
                        >
                            
                        </Item>
                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {getToken()?this.props.history.push('/person/forum'):this.props.history.push('/login')}}
                        >
                            
                        </Item>
                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {getToken()?this.props.history.push('/person/myFan'):this.props.history.push('/login')}}
                        >
                            
                        </Item>
                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {getToken()?this.props.history.push('/person/myFollows'):this.props.history.push('/login')}}
                        >
                            
                        </Item>
                        {getToken()?<Button type='warning' style={{marginTop:20}}  onClick={() =>
                            alert('', '?', [
                                { text: '', onPress: () => console.log('cancel') },
                                {
                                    text: '',
                                    onPress: () =>this.props.itemLogOut()
                                    /*new Promise((resolve) => {
                                        delToken()
                                        Toast.info('', 1);
                                        _this()
                                        resolve()

                                        //setTimeout(resolve, 1000);
                                    }),*/
                                },
                            ])
                        }></Button>:''}
                    </div>
                </div>:  <div>
                    <NavBar
                        mode="dark"
                        icon={<Icon type="left" />}
                        onLeftClick={() => this.props.history.goBack()}
                    ></NavBar>
                    <div className="container">
                        <div className="container-header">
                            <div className='container-header-main'>
                                <img src={this.props.image} alt=""/>
                                <Link to={`/person/info/${this.props.id}`} className='user'>
                                    <span>{this.props.name}</span>

                                    <Icon type='right
'/></Link>

                            </div>
                        </div>

                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {this.props.history.push(`/person/article/${this.props.id}`)}}
                        >
                            
                        </Item>
                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {this.props.history.push(`/person/forum/${this.props.id}`)}}
                        >
                            
                        </Item>
                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {this.props.history.push(`/person/fan/${this.props.id}`)}}
                        >
                            
                        </Item>
                        <Item
                            arrow="horizontal"
                            thumb="https://www.fengjinqi.com/static/img/.png"
                            onClick={() => {this.props.history.push(`/person/follows/${this.props.id}`)}}
                        >
                            
                        </Item>
                    </div>
                </div>}


            </Fragment>
            )

    }

}
LoginMain.propsTypes = {
    name:PropTypes.string,
    image:PropTypes.string,
    itemLogOut:PropTypes.func,
    history:PropTypes.func,
    type:PropTypes.bool,
    id:PropTypes.string


}
LoginMain.defaultProps = {
     image:'https://www.fengjinqi.com/static/img/pc-icon.png',
     type:true,
     id:''
}
export default LoginMain