import axios from '../utils/api.request'

/**
 * 
 * @param token
 */
export const getHomeList = (token)=>{
    return axios.request({
        url: '/api/article_list/',
      /*  headers: {
            'Authorization':'JWT '+''
        },*/
        method: 'get'
    })
}
/**
 * 
 * @param page
 */
export const getHomeListPage = (page)=>{
    return axios.request({
        url: '/api/article_list/?page='+page,
        /*  headers: {
              'Authorization':'JWT '+''
          },*/
        method: 'get'
    })
}
