import React,{Component} from 'react'
import ReactDOM from 'react-dom'
import { ActivityIndicator, WingBlank, ListView,SearchBar,PullToRefresh} from 'antd-mobile';
import {NavLink, HashRouter, Link} from 'react-router-dom'
import {connect} from 'react-redux'
import {actionCreators} from './store'
import {getArticleCate} from './../../api/article'
import {getCommentCount, getToken, getUser} from './../../utils/utils'
import './style.less'
class Home extends Component{
    constructor(props) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
        this.state = {
            dataSource,
            refreshing: true,
            isLoading: true,
            page:1,
            categrty:null,
            height: document.documentElement.clientHeight,
            useBodyScroll: false,
            search:true,
        };
        this.changeState = this.changeState.bind(this)
        this.submit = this.submit.bind(this)
    }
    changeState(list){
        const hei = this.state.height - ReactDOM.findDOMNode(this.lv).offsetTop;
        this.setState({
            height:hei,
            dataSource: this.state.dataSource.cloneWithRows(list)
        })
    }
      componentDidMount() {
        this.props.getHomeList()
          getArticleCate().then(res=>{
              this.setState({
                  categrty:res.data
              })
          })
          window.sessionStorage.clear()

    }
    componentWillReceiveProps(nextProps, nextContext) {
        this.changeState(nextProps.list)
        this.setState({
            refreshing: false,
            isLoading: false,
            search:false,
            height:document.documentElement.clientHeight - ReactDOM.findDOMNode(this.lv).offsetTop-50,
            page:(nextProps.page.next ? ++this.state.page:'')
        });

    }

    /**
     * TODO 
     * @param item
     * @returns {*}
     */
   /* getCommentCount(item){
        let count = 0;
        item.article_comment_set.map(el=>{
             count+=el.articlecommentreply_set.length
        })
        return count+=item.article_comment_set.length
    }*/
    submit(e){
        window.sessionStorage.clear()
        this.setState({
            search:true,
            page:1
        })
        window.sessionStorage.setItem('serachs',e)
        this.props.getSerach(e,1,'',true)
        document.getElementsByClassName('am-list-view-scrollview')[0].scrollTop=0
    }
   /**
    *TODO 
    */
    onRefresh=()=>{
        this.setState({ refreshing: true, isLoading: true,page:1 });
        this.props.getHomeList()
    }
    /**
     * TODO 
     */
    onEndReached = ()=>{
        if(this.state.page){
            this.setState({ refreshing: true, isLoading: true, });
            if (window.sessionStorage.getItem('serach')) {
                this.props.getSerach(window.sessionStorage.getItem('serach'),this.state.page,true)
            }else if(window.sessionStorage.getItem('serachs')){
                this.props.getSerach(window.sessionStorage.getItem('serachs'),this.state.page,true,true)
            }else{
                this.props.getHomePage(this.state.page)
            }


        }

    }


    serach(e){
        window.sessionStorage.clear()

        this.setState({
            search:true,
            page:1
        })
        window.sessionStorage.setItem('serach',e)
        this.props.getSerach(e,1,)
        document.getElementsByClassName('am-list-view-scrollview')[0].scrollTop=0

    }
    render() {
        //const {isShow} = this.props
        const row =  (rowData, sectionID, rowID) => {
            return(
                <HashRouter>
                <div className='list'>
                    <div>
                        <div className='list-header'>
                            <div className='list-header-left one-txt-cut'>
                                {getUser()? <Link to={JSON.parse(getUser()).id===rowData.authors.id?`/person/`:`/person/${rowData.authors.id}`}>
                                    <img src={rowData.authors.user_imag?rowData.authors.user_imag:rowData.authors.user_image} alt=""/>
                                    {rowData.authors.username}
                                </Link>
                                :    <Link to={`/person/${rowData.authors.id}`}>
                                        <img src={rowData.authors.user_imag?rowData.authors.user_imag:rowData.authors.user_image} alt=""/>
                                        {rowData.authors.username}
                                    </Link>
                                }
                                    {/*<Link to={`/person/${rowData.authors.id}`}>*/}
                                    {/*    <img src={rowData.authors.user_imag?rowData.authors.user_imag:rowData.authors.user_image} alt=""/>*/}
                                    {/*    {rowData.authors.username}*/}
                                    {/*</Link>*/}

                            </div>
                            <div className='list-header-right'><span>{rowData.category.name}</span></div>
                        </div>
                        {rowData.list_pic?
                            <div className='list-title'>

                                <div className='list-title-left txt-cut'>{rowData.title}</div>
                                <div className='list-title-right'><img src={rowData.list_pic} alt=""/></div>

                            </div>
                            :
                            <div className='list-title'style={{height:'auto'}}>
                                <div className='list-title-left txt-cut' style={{width:'100%'}}>{rowData.title}</div>
                            </div>
                        }
                        {/*<div className='list-title'>*/}
                        {/*    */}
                        {/*    <div className='list-title-left txt-cut'>{rowData.title}</div>*/}
                        {/*    {rowData.list_pic?<div className='list-title-right'><img src={rowData.list_pic} alt=""/></div>:''}*/}

                        {/*</div>*/}
                        <div className='list-desc'>
                            {rowData.desc}
                        </div>
                        <div className='list-footer'>
                            <div className='list-footer-left'>
                                <span className='click'>{rowData.click_nums}</span>
                                <span className='comment'>
                                    {getCommentCount(rowData)}
                                </span>
                            </div>
                            <div className='list-footer-right'><NavLink to={{pathname:`/article/detail/${rowData.id}`}}></NavLink></div>
                        </div>
                    </div>
                </div>
                </HashRouter>
            )
        };

        // if(list.results){
            return(
                <div>
                    <WingBlank>
                        <ActivityIndicator toast text="" animating={this.state.search} />
                    </WingBlank>
                    <SearchBar placeholder="Search" onSubmit={this.submit} />
                    <div className='nav'>
                        <div>
                            {this.state.categrty?this.state.categrty.map((item,index)=>{
                                return(
                                    <span key={item.id} onClick={()=>this.serach(item.id)}>{item.name}</span>
                                )
                            }):''}
                        </div>
                    </div>
                    <ListView
                        ref={el => this.lv = el}
                        dataSource={this.state.dataSource}
                        renderRow={row}
                        useBodyScroll={this.state.useBodyScroll}
                        style={{
                            height: this.state.height,
                        }}
                        renderFooter={() => (<div style={{ padding: 30, textAlign: 'center' }}>
                            {this.state.isLoading ? 'Loading...' : ''}
                        </div>)}
                        pullToRefresh={<PullToRefresh

                            refreshing={this.state.refreshing}
                            onRefresh={this.onRefresh}
                        />}
                        onEndReached={this.onEndReached}
                    />
                </div>
            )
       // /* }else{
       //      return null
       //  }*/

    }
}
const mapState = (state)=>({
    page:state.home.articleList,
    isShow:state.home.isShow,
    list:state.home.list

})
const mapDispatch = (dispatch) => ({
      getHomeList(){
         dispatch(actionCreators.getHome())
    },
    getHomePage(page){
          dispatch(actionCreators.getHomePage(page))
    },
    getSerach(name,page,type,n){
          dispatch(actionCreators.getSerachAxios(name,page,type,n))
    }
})
export default connect(mapState,mapDispatch)(Home)
